import React from 'react'
import gai from '../Img/output.gif'
import zoio from '../Img/zoio.png'

class Home extends React.Component {

    constructor(){
    super()
        this.state = {
            hour: null
            }
        
    }
    componentDidMount(){
        fetch('http://worldtimeapi.org/api/timezone/America/Sao_Paulo').then(
            time => time.json()
        ).then(
            data => this.setState({ hour: parseInt(data.datetime.split('T')[1].split(':')[0])})
        )
    }

    render(){
        var greeting = null
        if(this.state.hour>=6 && this.state.hour <=12){
            greeting = <span>Bom dia</span> 
            }
        
        else if(this.state.hour>=13 && this.state.hour <=18){
            greeting = <span>Boa tarde</span> 
                }

        else {
            greeting = <span>Boa noite</span>
        }

        return (
            <div className="container" style={{padding:"30px"}}>
            <div className="row">
                <div className="col">
                <p style={{fontSize:"40px",textAlign:"center"}}>Este são alguns dos poucos, porém fascinantes projetos os quais participei</p>
                </div>
            </div>
            
            <div className="row" style={{marginTop:"50px"}}>
                <div className="col">
                    <h2>
                        Bot do twitter Maito Gai
                    </h2>
                    <p style={{fontSize:"19px",marginTop:"30px"}}>O perfil do twitter @GaiGenerator foi um bot codificado por mim em python o qual ao mencioná-lo em algum tweet, dentro de alguns segundos ele irá lhe responder com 
                        um gif de sua foto de perfil da conta twitter no lugar do rosto do Maito Gai (Personagem de naruto)
                    </p>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <img src={gai} className="gai"/>
                    <p style={{textAlign:"center"}}>Exemplo de gif feito pelo bot</p>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <p style={{fontSize:"19px",marginTop:"30px"}}>Caso tenha interesse, sinta-se a vontade para marcar o <a href="http://www.twitter.com/GaiGenerator">Bot</a></p>
                </div>
            </div>

            <div className="row" style={{marginTop:"80px"}}>
                <div className="col">
                    <h2>
                        Bot do discord Everson Zóio
                    </h2>
                    <p style={{fontSize:"19px",marginTop:"30px"}}>Um bot do discord que fiz em python para um servidor de amigos meus para manter placar do league of legends e mostrar o rank e elo de quem está no servidor
                    </p>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <img src={zoio} className="gai"/>
                    <p style={{textAlign:"center"}}>Bot em funcionamento</p>
                </div>
            </div>
        </div>
        )}
                
}
export default Home