import React from 'react'
import Typewriter from 'typewriter-effect'
import MrRobot from '../Img/mrrobot.gif'

class Home extends React.Component {

    render(){
        return (
            <div className="container">
            <div id="typewriter-text" className="row">
            <Typewriter
                options={{
                    strings: ['MERN Stack','MongoDB','Express.js','React.js','Node.js'],
                    autoStart: true,
                    loop: true,
                    deleteSpeed:70,
                    delay:90
                }}
            />
            </div>
            <div className="row">
                <div className="col-sm intro">
                        <p className="intro-text">
                            Agilidade, Design responsivo e Interatividade são elementos chaves para o design de aplicativos e websites atualmente, por isso convido-o a conhecer mais sobre meu trabalho
                        </p>
                </div>
            </div>
            <div className="row">
                <div className="col-sm">
                        <img width="500px" src={MrRobot} className="mrrobot"/>
                </div>
            </div>
            <div className="row">
                <div className="col-sm intro">
                        <p className="more-text">
                            Acessando a aba <a href="/sobre">Sobre</a> descubra mais sobre mim e confira também <a href="/projetos">Projetos</a> realizados por mim
                        </p>
                </div>
            </div>
        </div>
        )}
                
}
export default Home