import React from 'react'
import peep from '../Img/peep.jpg'
import {Jumbotron,Button} from 'reactstrap'

class Home extends React.Component {

    constructor(){
    super()
        this.state = {
            hour: null
            }
        
    }
    componentDidMount(){
        fetch('http://worldtimeapi.org/api/timezone/America/Sao_Paulo').then(
            time => time.json()
        ).then(
            data => this.setState({ hour: parseInt(data.datetime.split('T')[1].split(':')[0])})
        )
    }

    render(){
        var greeting = null
        if(this.state.hour>=6 && this.state.hour <=12){
            greeting = <span>Bom dia</span> 
            }
        
        else if(this.state.hour>=13 && this.state.hour <=18){
            greeting = <span>Boa tarde</span> 
                }

        else {
            greeting = <span>Boa noite</span>
        }

        return (
            <div className="container" style={{padding:"30px"}}>
            <div className="row">
                <div className="col-sm">
                    <img className="peep" src={peep}/>
                </div>
                <div className="col-sm">
                <Jumbotron>
                    <h1 className="display-3" style={{color:"black"}}>{greeting}!</h1>
                    <p className="lead" style={{color:"black"}}>Meu nome é Rodrigo Henrique, graduando em Engenharia de Computação na Universidade Federal de Uberlândia</p>
                    <hr className="my-2" />
                    <p style={{color:"black"}}>Sou co-fundador e developer da startup <a href="http://www.homero.tech">Homero.tech</a>. 
                    Tenho conhecimentos do MERN Stack, python e gosto de trabalhar com desenvolvimento full-stack.<br/>
                    Caso queira ver alguns exemplos de códigos feitos por mim, pode acessar meu github atráves do botão abaixo </p>
                    <a href="http://www.github.com/r-drg"><Button color="dark">Github</Button></a>
                </Jumbotron>
                </div>
            </div>
        </div>
        )}
                
}
export default Home